import React from 'react';

import styles from './FormUrlShortener.module.css';

const backgroundImage = `url('${process.env.PUBLIC_URL}/assets/background-home.jpg')`;

class FormUrlShortener extends React.Component {
  constructor(props) {
    super(props);
    
    this.state = {
      inputUrl: null
    };

    this.shortenUrl = this.shortenUrl.bind(this);
    this.handleOnChangeInput = this.handleOnChangeInput.bind(this);
  }

  shortenUrl() {
    const url = this.state.inputUrl;

    if (!url || !url.length) {
      return;
    }
  
    console.info(url);
  };

  handleOnChangeInput(event) {
    this.setState({
      inputUrl: event.currentTarget.value.trim(),
    });
  }

  render() {
    return (
      <form className={styles.form} style={{ backgroundImage }}>
        <div className={styles.form_content}>
          <h1 className={styles.title}>Shorten your links</h1>
          <p className={styles.description}>
            Links are long. Shorten the links you want to share and keep
            track of them while people navigate on the internet.
          </p>
          <div className={styles.input_container}>
            <div className={styles.input_wrapper}>
              <input
                className={styles.input}
                type="text"
                placeholder="Paste your link here"
                onChange={this.handleOnChangeInput}
              />
            </div>
            <input
              className={styles.shorten_button}
              type="button"
              value="Shorten"
              title="Click to shorten your link"
              onClick={this.shortenUrl}
            />
          </div>
        </div>
      </form>
    );
  };
};

export default FormUrlShortener;
